.App {
  width: 100vw;
  height: 100vh;
  background: linear-gradient(45deg, #667eea 0%, #764ba2 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Arial', sans-serif;
}

.game-container {
  display: flex;
  align-items: flex-start;
  gap: 40px;
  padding: 20px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.game-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  color: white;
}

.game-info h1 {
  font-size: 2.5rem;
  margin: 0;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.score-board {
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-size: 1.2rem;
  font-weight: bold;
}

.start-button {
  background: #4CAF50;
  color: white;
  border: none;
  padding: 15px 30px;
  border-radius: 10px;
  font-size: 1.1rem;
  cursor: pointer;
  transition: all 0.3s;
  font-weight: bold;
}

.start-button:hover {
  background: #45a049;
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(76, 175, 80, 0.4);
}

.game-board {
  border: 2px solid #333;
  background: #000;
  padding: 10px;
  border-radius: 10px;
}

.stage {
  display: grid;
  grid-template-rows: repeat(20, 1fr);
  grid-template-columns: repeat(12, 1fr);
  gap: 1px;
  background: #333;
  padding: 5px;
  border-radius: 5px;
}

.cell {
  width: 25px;
  height: 25px;
  background: #000;
  border-radius: 2px;
}

.next-piece {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  color: white;
}

.next-piece h3 {
  margin: 0;
  font-size: 1.5rem;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.next-stage {
  display: grid;
  grid-template-rows: repeat(4, 1fr);
  grid-template-columns: repeat(4, 1fr);
  gap: 1px;
  background: #333;
  padding: 10px;
  border-radius: 10px;
  border: 2px solid #555;
}

.next-stage .cell {
  width: 20px;
  height: 20px;
}

@media (max-width: 768px) {
  .game-container {
    flex-direction: column;
    gap: 20px;
    padding: 15px;
  }
  
  .game-info h1 {
    font-size: 2rem;
  }
  
  .cell {
    width: 20px;
    height: 20px;
  }
  
  .next-stage .cell {
    width: 15px;
    height: 15px;
  }
} 